import "./ContactForm.css";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";

export const ContactForm = () => {
  //      Yup
  const validationSchema = Yup.object().shape({
    name: Yup.string().required(",   "),
    email: Yup.string()
      .email("   ")
      .required(",   email"),
    agreement: Yup.boolean().oneOf(
      [true],
      "      "
    ),
  });

  interface Props {
    name: string;
    email: string;
    agreement: boolean;
  }

  //   
  const handleSubmit = (values: Props) => {
    console.log(" :", values);
  };

  return (
    <Formik
      initialValues={{
        name: "",
        email: "",
        file: null,
        agreement: false,
      }}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ setFieldValue }) => (
        <Form>
          <div>
            <div className="wrapperFormName">
              <label className="inputLabel" htmlFor="name">
                  ?
              </label>
              <Field
                className="fieledForm"
                type="text"
                id="name"
                name="name"
                onFocus={(e: {
                  target: {
                    previousSibling: {
                      classList: { add: (arg0: string) => any };
                    };
                  };
                }) => e.target.previousSibling.classList.add("hidden")}
                onBlur={(e: {
                  target: {
                    value: string;
                    previousSibling: {
                      classList: { remove: (arg0: string) => any };
                    };
                  };
                }) =>
                  e.target.value === "" &&
                  e.target.previousSibling.classList.remove("hidden")
                }
              />
            </div>
            <ErrorMessage name="name" component="div" />
          </div>

          <div>
            <div className="wrapperFormEmail">
              <label className="inputLabel" htmlFor="email">
                 -mail
              </label>
              <Field
                className="fieledForm"
                type="email"
                id="email"
                name="email"
                onFocus={(e: {
                  target: {
                    previousSibling: {
                      classList: { add: (arg0: string) => any };
                    };
                  };
                }) => e.target.previousSibling.classList.add("hidden")}
                onBlur={(e: {
                  target: {
                    value: string;
                    previousSibling: {
                      classList: { remove: (arg0: string) => any };
                    };
                  };
                }) =>
                  e.target.value === "" &&
                  e.target.previousSibling.classList.remove("hidden")
                }
              />
            </div>
            <ErrorMessage name="email" component="div" />
          </div>

          <div className="inputLabelType">
            <label htmlFor="file" className="inputSkrin">
               
              <br />
              <span className="spanType">.png / .jpg / .pdf</span>
              <input
                className="inputSkrin"
                type="file"
                id="file"
                name="file"
                onChange={(event) => {
                  const file = event.currentTarget.files?.[0];
                  setFieldValue("file", file);
                }}
              />
            </label>
          </div>

          <button className="buttonSubmit" type="submit">
            
          </button>

          <div className="checkboxWrapper">
            <label className="fieldForm">
              <input
                type="checkbox"
                id="agreement"
                name="agreement"
                className="customCheckbox"
              />
                  
            </label>
          </div>
        </Form>
      )}
    </Formik>
  );
};
